import React from 'react';
import PropTypes from 'prop-types';
import Styles from './table.module.css';
// import TableBodyComponent from './table-body/TableBodyComponent';
import TableFooterComponent from './table-footer/TableFooterComponent';

function TableComponent({
  component,
  paginationParameters,
  hasExport,
  onExport,
  tableHeader,
  stylesTable,
  paginate,
  isTableBodyLoading,
}) {
  return (
    <div className={Styles.boxShadow}>
      <table className={Styles.table}>
        <thead>
          {/* <TableHeaderComponent
              tableHeaderColumns={tableHeaderColumns}
              sortHeader={sortHeader}
              filterModel={filterModel}
              setFilterModel={setFilterModel}
              sortColumn={sortColumn}
              sortBy={sortBy}
              filterList={filterList}
              filterShow={filterShow}
            /> */}
          {tableHeader}
        </thead>
        <div
          className={stylesTable !== null ? stylesTable : Styles.tableBodyDiv}
        >
          <tbody className={Styles.tableBody}>
            {component}
            {' '}
            {/* <TableBodyComponent component={component} /> */}
          </tbody>
        </div>
      </table>
      <TableFooterComponent
        paginationParameters={paginationParameters}
        component={component}
        hasExport={hasExport}
        onExport={onExport}
        paginate={paginate}
        paginationData={paginationParameters}
        isTableBodyLoading={isTableBodyLoading}
      />
    </div>
  );
}
TableComponent.propTypes = {
  component: PropTypes.func,
  paginationParameters: PropTypes.shape.isRequired,
  hasExport: PropTypes.bool,
  onExport: PropTypes.func.isRequired,
  tableHeader: PropTypes.func.isRequired,
  stylesTable: PropTypes.shape,
  paginate: PropTypes.func.isRequired,
  isTableBodyLoading: PropTypes.bool,
};
TableComponent.defaultProps = {
  component: null,
  hasExport: false,
  stylesTable: null,
  isTableBodyLoading: true,
};
export default TableComponent;
